<!--  -->
<template>
    <div class="home">
        <el-container>
            <el-header height=""><MyHeader/></el-header>
            <el-container>
                <el-aside><MyAside/></el-aside>
                <el-main>
                    <router-view/>
                </el-main>
            </el-container>
        </el-container>
        <div class="ad">广告位招租</div>
        <div class="watermark">付费去水印</div>
        <div class="bg"></div>
    </div>
</template>


<script>
import MyHeader from '@/components/MyHeader.vue'
import MyAside from '@/components/MyAside.vue'
import { matchRoutes } from '@/utils/matchRoutes'
import { dynamicRoutes } from '@/router';
export default {
    name: 'MyHome',

    components: {
        MyHeader,
        MyAside,
    },
    // 刷新时，先添加routes，在渲染
    created() {
        this.$store.commit("GETUSER")
        for (let item of matchRoutes(this.$store.state.UserStore.auth, dynamicRoutes)) {
            this.$router.addRoute(item)
        }
    },
}
</script>


<style lang="less">
    .el-header {
        background-color: #409EFF;
    }
    .ad {
        font-size: 40px;
        position: fixed;
        top: 50%;
        transform: translateY(-50%);
        right: 2%;
        width: 1em;
        text-align: center;
        border: 8px solid red;
        padding: 10px;
    }
    .watermark {
        pointer-events: none; // 不做事件响应
        position: fixed; // 定位

        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); // 居中


        font-size: 100px;
        opacity: 0.2; // 半透明
    }
    .bg {
        width: 100vw;
        height: 100vh;

        pointer-events: none;
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 100px;
        opacity: 0.1;
        background-image: url('../assets/earth/earth.jpg');

        background-repeat:repeat;
    }
</style>