<!-- Header -->
<template>
    <div class="header">
        <div class="front">
            <ul>
                <li><el-avatar shape="square" :size="35" :src="avatarUrl" /></li>
                <li><a href="javascript:;"><router-link to="/Login"><span>{{ $store.state.UserStore.user.id }}</span></router-link></a></li>
                <li>
                    <a href="javascript:;">
                        <div v-if="$store.state.UserStore.user">
                            <el-popconfirm
                            @confirm="LogOut"
                            title="确认退出登录吗？"
                            >
                                <span  slot="reference">退出登录</span>
                            </el-popconfirm>
                        </div>
                        
                        <span @click="$router.replace({ path: '/Login' })" v-else>登录</span>
                    </a>
                </li>
                <!-- <li><a href="javascript:;"><router-link to="/Dashboard"><span>大屏</span></router-link></a></li> -->
            </ul>
        </div>
    </div>
</template>


<script>
import { resetRoutes } from '@/router';
export default {
    name: 'MyHeader',
    data() {
        return {
            avatarUrl:"https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png",
            user:'',
        }
    },
    methods: {
        setDefaultRoutes() {

        },
        LogOut() {
            this.$router.replace({ path: '/Login' })
            this.$store.dispatch("logout")

            // 退出登录还原静态路由
            this.$store.commit("GETUSER")
            resetRoutes()
            console.log(this.$router.getRoutes())

        }
    },
    mounted() {
        this.$store.commit("GETUSER")
        this.user = this.$store.state.UserStore.user.id
    },
}
</script>


<style scoped lang="less">
    .header {
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        ul {
            display: flex;
            align-items: center;
            list-style-type: none;
        }
        li {
            display: inline-block;
            margin-right: 20px;
            a {
                text-decoration: none;
            }
            span {
                color:white;

                &:hover {
                    color: #C0C4CC;
                }
            }


        }
    }
</style>